.App {
  min-height: 100vh;
  background-image: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
  color: whitesmoke;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
}

.header1 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
}


.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: darkcyan;
  border: none;
  padding: 10px 20px;
  color: whitesmoke;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}